////
/// Comments component.
///
/// This component wraps the entire comments section.
///
/// @group  Components
/// @author Sky Shabatura <theme@sky.camp>
/// @link   https://taproot-theme.com
////

.comments {

	// Header/title for the comments section.
	&__title {}

	// Comments list wrapper.
	&__list {

        list-style: none;
        padding: 0;

		// Children lists (note that WP doesn't have a filter -- the
		// only way to change this class is with a custom Walker).
		// @link https://core.trac.wordpress.org/ticket/44923
		.children {
            list-style: none;
            padding-left: var( --typography--body--block-spacing );

            // mobile styles 
            @include screen('mobile') {
                padding-left: 0.62em;
            }   
        }
	}

	// Comments closed message.
	&__closed {}
}
